<script>
import emitter from '@/components/helpers/emitter';
import Cropper from 'cropperjs';

export default {
  data() {
    return {
      cartList: [],
      cartTotal: null,
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: '',
        },
        message: '',
        options: {
          pageAction: 'add-company',
          companyName: '',
          industryCategory: '',
          CompanyAddress: '',
          CompanyAddressDetail: '',
          companyInfo: '',
          contactPosition: '',
          imagesUrl: [''],
          addJobsToken: 1,
        },
      },
      formState: false,
    };
  },
  methods: {
    loadingImg(e) {
      const nowId = e.target.dataset.id;
      console.log(nowId);
      const refsItem = `cropImg${nowId}`;
      //   this.$refs[refsItem].click();
      console.log(refsItem);
      let CROPPER;
      const reader = new FileReader();
      if (e.target.files[0]) {
        reader.readAsDataURL(e.target.files[0]);
        reader.onload = (event) => {
          console.log(event);
          const dataURL = reader.result;
          console.log(this.$refs[refsItem]);
          this.$refs[refsItem].src = dataURL;
          const image = this.$refs[refsItem];
          // cropper-----------------------------------------
          CROPPER = new Cropper(image, {
            aspectRatio: 16 / 9,
            viewMode: 0,
            minContainerWidth: 320,
            minContainerHeight: 320,
            dragMode: 'move',
            preview: [
              document.querySelector('.previewBox'),
            ],
          });
          console.log(CROPPER);
        };
      }
    },
    checkFile(e) {
      const nowId = e.target.dataset.id;
      const refsItem = `sendFormInfoImgs${nowId}`;
      const file = this.$refs[refsItem].files[0];
      // console.dir(this.$refs[refsItem]);
      console.dir(file);
      const formData = new FormData();
      console.log(formData);
      formData.append('companyImgs', file);
      console.log(formData);
      this.uploadFile(formData, nowId);
    },
    uploadFile(formData, nowId) {
      // const imgurToken = 'Client-ID 4ef4ca52de4b42c';
      // const url = 'https://api.imgur.com/3/image';
      console.dir(formData, nowId);
      this.$http({
        method: 'POST',
        url: 'https://api.imgur.com/3/image',
        data: formData,
        headers: {
          Authorization: 'Client-ID ef6e862acf052df',
        },
        mimeType: 'multipart/form-data',
      })
        .then((res) => {
          console.log(res.data);
          this.options.imagesUrl[nowId] = res.data.data.link;
          // console.log(res.data.data.link);
        })
        .catch((e) => {
          console.log(e);
        });
    },
    checkCart() {
      if (this.form.options.CompanyAddress !== '') {
        this.form.user.address = `${this.form.options.CompanyAddressDetail}
      ${this.form.options.CompanyAddress}`;
        if (this.$refs.sendFormInfoCompanyInfo !== '') {
          this.getCart();
          this.formState = true;
          this.sendForm();
        } else {
          this.formState = false;
          console.log('');
        }
      } else {
        console.log('');
      }
    },
    sendForm() {
      this.addCartJob(this.form.options.addJobsToken);
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/order`;
      if (this.formState === false) {
        console.log('');
        this.deleteCart();
      } else {
        const formData = {
          data: this.form,
        };
        this.$http
          .post(url, formData)
          .then((res) => {
            console.log(`${res.data.message}`);
            console.log(res);
            this.deleteCart();
            // this.$router.push('/');
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    addImageUrl() {
      this.form.options.imagesUrl.push('');
    },
    deleteImageUrl() {
      this.form.options.imagesUrl.pop();
    },
    deleteCart() {
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/carts`;
      this.$http
        .delete(url)
        .then((res) => {
          console.log(`${res.data.message}`);
          this.getCart();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    addCartJob(qty = 1) {
      const id = '-MctDx8Qj8Tmw9eJoZqN';
      const product = { data: { product_id: id, qty } };
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .post(url, product)
        .then((res) => {
          console.log(`${res.data.message}:${id}`);
          emitter.emit('get-cart');
          // this.$refs.productModal.modal.hide();
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getCart() {
      // emitter.emit('spinner-open');
      const url = `${process.env.VUE_APP_API}/api/${process.env.VUE_APP_PATH}/cart`;
      this.$http
        .get(url)
        .then((res) => {
          console.log(res);
          this.cartList = res.data.data.carts;
          this.cartTotal = res.data.data.final_total;
          // emitter.emit('spinner-close');
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
  created() {
    this.deleteCart();
  },
  mounted() {
    emitter.emit('close-cart');
  },
};
</script>

